@import url(https://fonts.googleapis.com/css?family=Oswald);
body {
  font-family: Oswald;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin: 100px 100px 100px 100px;
  background-color: #9d5c63;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  color: #d6e3f8;
}

.btn {
  background-color: #e4bb97;
  border-color: #e4bb97;
  color: lemonchiffon;
}
#prbar {
    margin: 5px;
    width: 500px;
    background-color: #584d53;
    overflow: hidden;
    border: 1px solid #584d53;
    border-radius: 5px;
    box-shadow: 0px 2px 4px #555555;
    display: inline-block;
}

#prpos {
    height: 50px;
    background-color: #d6e3f8;
    border-right: 1px solid #584d53;
    transition: width 2s ease;
}

#clickable-div:hover {
    background-color: lightblue;
    cursor: pointer;
}

.buttons {
    margin: 30px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.popup {
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, 0.5);
}

.popup_content {
    position: absolute;
    left: 30%;
    top: 20%;
    width: 40%;
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    border: 2px solid black;
}


